import { useState } from 'react';
import axios from 'axios';
import { useRouter } from 'next/router';
const Register = () => {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    password: '',
    phoneNumber: '',
    countryCode: ''
  });
  const router = useRouter();
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('https://tinytales.trendline.marketing/api/register', formData);
      router.push('/login'); // التوجيه بعد التسجيل الناجح.
    } catch (error) {
      console.error('Error registering:', error);
    }
  };
  return (
    <form onSubmit={handleSubmit}>
      {/* إضافة الحقول في الاستمارة */}
      <input type="text" name="fullName" placeholder="Full Name" onChange={handleChange} required />
      <input type="email" name="email" placeholder="Email" onChange={handleChange} required />
      <input type="password" name="password" placeholder="Password" onChange={handleChange} required />
      <input type="text" name="phoneNumber" placeholder="Phone Number" onChange={handleChange} required />
      <input type="text" name="countryCode" placeholder="Country Code" onChange={handleChange} required />
      <button type="submit">Register</button>
    </form>
  );
};
export default Register;
